<template>
  <div class='root'>
    <h3>Заброшенные деревни Алнашского района</h3>
    <l-map
      zoom=10
      :center="center"
      id="mymap"
      ref="mymap"
    >
      <l-tile-layer
        :url="url"
        :attribution="attribution"
      />
    </l-map>
    <v-row>
      <v-col cols="2"></v-col>
      <v-col cols="8">
        <p>
          Исходную таблицу с названиями и координатами деревень я открыл в QGIS
          и создал на ее основе слой с точечными объектами. Этот слой был сохранен
          в формате GeoJSON, который автоматически загружается сюда при переходе на страницу.
          На странице использована библиотека Leaflet с плагином MarkerCluster.
        </p>
      </v-col>
      <v-col cols="2"></v-col>
    </v-row>
  </div>
</template>

<script>
import { LMap, LTileLayer } from 'vue2-leaflet'
import L from 'leaflet'
import 'leaflet/dist/leaflet.css'
import 'leaflet.markercluster/dist/MarkerCluster.css'
import 'leaflet.markercluster/dist/MarkerCluster.Default.css'
import 'leaflet.markercluster/dist/leaflet.markercluster.js'

delete L.Icon.Default.prototype._getIconUrl
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
})

export default {
  name: 'Villages',
  components: {
    LMap,
    LTileLayer
  },
  data () {
    return {
      center: L.latLng(56.8, 53.8),
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution: '&copy; <a href="https://osm.org/copyright">OpenStreetMap</a> contributors',
      villages: {
        features: []
      },
      baseUrl: process.env.VUE_APP_BASE_URL
    }
  },
  async mounted () {
    const response = await fetch('data/villages_alnashy.geojson')
    const geojson = await response.json()
    this.villages = geojson
    this.$nextTick(() => {
      const map = this.$refs.mymap.mapObject
      const fg = new L.FeatureGroup()
      const mg = L.markerClusterGroup()
      for (const i of this.villages.features) {
        const obj = L.geoJSON(i)
        console.log(i)
        obj.bindPopup(i.properties.Field1)
        obj.eachLayer(function (layer) {
          layer.bindPopup(i.properties.Field1)
        })
        fg.addLayer(obj)
        mg.addLayer(obj)
      }
      mg.addTo(map)
      const bounds = mg.getBounds()
      map.fitBounds(bounds)
    })
  }
}
</script>

<style>
#mymap {
  height: calc(100vh - 50px);
}

.root > * {
  margin: 10px auto;
}

h3 {
  text-align: center;
}
</style>
